import React from 'react';
import ReactDOM from 'react-dom';
import './index.css';
import postdata from './thing.json'
  

import * as serviceWorker from './serviceWorker';
var ques = postdata.map((postdetail,index)=>{
  return postdetail.questions });
var o1 = postdata.map((postdetail,index)=>{
  return postdetail.option1 });
var o2 = postdata.map((postdetail,index)=>{
  return postdetail.option2 });
var o3 = postdata.map((postdetail,index)=>{
  return postdetail.option3 });
var o4 = postdata.map((postdetail,index)=>{
  return postdetail.option4 });
var o1a = postdata.map((postdetail,index)=>{
  return postdetail.o1 });
var o2a = postdata.map((postdetail,index)=>{
  return postdetail.o2 });
var o3a = postdata.map((postdetail,index)=>{
  return postdetail.o3 });
var o4a = postdata.map((postdetail,index)=>{
  return postdetail.o4 });
  
var ques_count= 2;


function App() {
  
  return (
    <div class="area" >
                <div class="ques">
                    <div class="qbox">
                    <p>{ques[ques_count-2]} </p>
                    </div>
                    <div class="options">
                        <div class="_op_div">
                            <div class = "option" id ={"q_"+(ques_count-1)+"_p_1"} data-value={o1a[ques_count-2]} onClick={e => changecolor(e.target.getAttribute('id'))}>
                                <p>{o1[ques_count-2]}</p>
                            </div>
                            <div class = "option" id ={"q_"+(ques_count-1)+"_p_2"} data-value={o2a[ques_count-2]} onClick={f => changecolor(f.target.getAttribute('id'))}>
                                <p>{o2[ques_count-2]}</p>
                            </div>
                        </div> 
                        <div class="_op_div">
                            <div class = "option" id ={"q_"+(ques_count-1)+"_p_3"} data-value={o3a[ques_count-2]} onClick={g => changecolor(g.target.getAttribute('id'))}>
                                <p>{o3[ques_count-2]}</p>
                            </div>
                            <div class = "option"  id ={"q_"+(ques_count-1)+"_p_4"} data-value={o4a[ques_count-2]} onClick={h => changecolor(h.target.getAttribute('id'))}>
                                <p>{o4[ques_count-2]}</p>
                            </div>
                        </div> 
                    </div>
                    
                </div>
                <div class="ques">
                    <div class="qbox">
                    <p>{ques[ques_count-1]}</p>
                    </div>
                    <div class="options">
                        <div class="_op_div">
                            <div class = "option" id ={"q_"+(ques_count)+"_p_1"} data-value={o1a[ques_count-1]} onClick={l => changecolor(l.target.getAttribute('id'))}>
                                <p>{o1[ques_count-1]}</p>
                            </div>
                            <div class = "option" id ={"q_"+(ques_count)+"_p_2"} data-value={o2a[ques_count-1]} onClick={z => changecolor(z.target.getAttribute('id'))}>
                                <p>{o2[ques_count-1]}</p>
                            </div>
                        </div> 
                        <div class="_op_div">
                            <div class = "option" id ={"q_"+(ques_count)+"_p_3"} data-value={o3a[ques_count-1]} onClick={c => changecolor(c.target.getAttribute('id'))}>
                                <p>{o3[ques_count-1]}</p>
                            </div>
                            <div class = "option" id ={"q_"+(ques_count)+"_p_4"} data-value={o4a[ques_count-1]} onClick={v => changecolor(v.target.getAttribute('id'))}>
                                <p>{o4[ques_count-1]}</p>
                            </div>
                        </div> 
                    </div>
                    
                </div>
            </div>
  );
}

function Createbut(){
    return(<button id="answer" onClick={()=> check()}>Submit</button>);
}
var len_ques = ques.length;



for(var j=0;j<len_ques/2;j++){
ReactDOM.render(<App />,
  document.getElementsByClassName('area_area')[j]
);ques_count= ques_count+2;}


ReactDOM.render(
  <Createbut />,
  document.getElementById('but')
);


