// unregister() to register() below. Note this comes with some pitfalls.
// Learn more about service workers: https://bit.ly/CRA-PWA

function changecolor(clicked_id){
  console.log(clicked_id)
  let allbut = document.getElementsByClassName('option_click')
  let s1=allbut.length

  let s=clicked_id;
 
  var q=s.length;
  let num =''
  for(let j=0;j<q;j++){
      
      if(s[j]=='_'){
          num=s[j+1]
          break;
      }
  }
  
  for(let i=0 ; i<4;i++){
      console.log('q_'+num+'_p_'+(i+1))
      document.getElementById('q_'+num+'_p_'+(i+1)).className='option';
  }
  document.getElementById(clicked_id).className='option_click';
}

function check(){

  let correct=0;
  let sel = document.getElementsByClassName('option_click');
  let unselected = document.getElementsByClassName('option');
  
  let len =sel.length;
  let num='';
  
  for(let i=0;i<len;i++){
      let p_select = sel[i].getAttribute('data-value')
      if(p_select == '1_0_0_1'){
          correct=correct+1;
          sel[i].className='selected_correct';
          i=i-1;
          len=len-1;
      }
      else {
          
          console.log(i)
          console.log(sel[i])
          let id_ = sel[i].getAttribute('id');
          for(let j=0;j<7;j++){
              console.log(id_[j])
              if(id_[j]=='_'){
                  num=id_[j+1]
                  break;
              }
          }
          for(j=1;j<=4;j++){
              let val_ops = document.getElementById('q_'+num+'_p_'+(j)).getAttribute('data-value');
              if(val_ops == '1_0_0_1'){
                  document.getElementById('q_'+num+'_p_'+(j)).className='notselected_correct';
              }
              
          }
          sel[i].className='selected_incorrect';
          i=i-1;
          len=len-1;
      }
  }
  let k=0;
  let len1 = unselected.length;
  while (k<len1){
      console.log(k)
      if(unselected[k].getAttribute('data-value') == '1_0_0_1'){
          unselected[k].className = "notselected_correct"
          len1=len1-1;
          k=k-1;
      }
      k=k+1;
  }
  let len2 = document.getElementsByClassName('option').length;
  for(let i=0;i<len2;i++){
    document.getElementsByClassName('option')[i].style.pointerEvents="none";
  }
  let len3 = document.getElementsByClassName('option_click').length;
  for(let i=0;i<len3;i++){
    document.getElementsByClassName('option_click')[i].style.pointerEvents="none";
  }
  let len4 = document.getElementsByClassName('notselected_correct').length;
  for(let i=0;i<len4;i++){
    document.getElementsByClassName('notselected_correct')[i].style.pointerEvents="none";
  }
  let len5 = document.getElementsByClassName('selected_incorrect').length;
  for(let i=0;i<len5;i++){
    document.getElementsByClassName('selected_incorrect')[i].style.pointerEvents="none";
  }
  let len6 = document.getElementsByClassName('selected_correct').length;
  for(let i=0;i<len6;i++){
    document.getElementsByClassName('selected_correct')[i].style.pointerEvents="none";
  }
  let len7 = document.getElementsByClassName('ques').length;

 document.getElementById('instructions').getElementsByTagName('p')[0].innerHTML = "Your Final Score : "
 document.getElementById('instructions').getElementsByTagName('li')[0].innerHTML = " Total No of Questions :   "+ "   "+String(len7)
 document.getElementById('instructions').getElementsByTagName('li')[1].innerHTML = "No of Correct answers :   "+ "   "+String(len6)
 document.getElementById('instructions').getElementsByTagName('li')[2].innerHTML = "No of Incorrect answers :   "+ "   "+String(len5)
 document.getElementById('instructions').getElementsByTagName('li')[3].innerHTML = "No of Unanswered Questions :   "+ "   "+String(len7 -(len6+len5))
 document.getElementById('finishing_inst_line').innerHTML = "Check your responses below."
}
serviceWorker.register();

